.picture-container {
    // // display: flex;
    width: 320px;
    align-items: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    display: grid;
    grid-template-columns: repeat(2, 200px);
    margin-left: 10%;
    margin-right: 10%;
    @media (min-width: 768px) {
        margin-left: 32%;
        margin-right: 10%;
    }

    .picture-box {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;

        .picture-image {
            display: flex;
            width: 152px;
            height: 152px;
            justify-content: center;
            align-items: center;

            .post-preview {
                width: 152px;
                height: 152px;
                flex-shrink: 0;
            }
        }
    }
}
.number-container {
    display: flex;
    padding: 20px 10px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    align-self: stretch;

    .number-box {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 4px;

        .number {
            display: flex;
            width: 16px;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            color: var(--Black, #1c1c1e);
            font-family: Pretendard;
            font-size: 16px;
            font-style: normal;
            font-weight: 200;
            line-height: normal;
        }
        .active {
            color: #a431b9;
        }
    }
}
